@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap');

/* cssReset */
body, h1, h2, h3, ul, li, div, p, form, fieldset, legend{
    margin:0;
    padding:0;
    border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
*{
    font-family:"Roboto", "Noto Sans Korean", "맑은 고딕", "나눔고딕", "돋움";
    font-size: 1.6rem;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
ul, li{
    list-style:none;
}
a{
    color:inherit;
    text-decoration: none;
}
input{
    padding: 1rem;
    box-sizing: border-box;
}
img, fieldset{
    border:0 none
}
button{
    cursor:pointer;
    border: none;
}
address{
    font-style:normal
}
img{
    max-width:100%;
    vertical-align:top;
    height: auto;
}
html, body{
    width: 100vw;
    font-size: 62.5%;
    margin: auto;
    overflow-x: clip;
}


.hide{
    position:absolute;
    left:-9999px;
    top:-9999px
}

.media_q{
    display: none;
}

.container{
    padding-top: 12rem;
}

.left_p {
    margin: 0 10rem;
}

/* 반응형 */
html, body{font-size: 62.5%;}

/* 데스크탑 */

@media screen and (min-width:1025px)and (max-width:1920px){
    html, body{font-size: 57.8%;}
}

/* 테블릿 */
@media screen and (min-width:769px)and (max-width:1024px){
    html, body{font-size: 54%;}
}

/* 모바일 */

@media screen and (min-width:360px)and (max-width:768px){
    html, body{font-size: 48%;}
}

